 media-narrowing($media-narrowing) {\n    @media (max-width: $media-narrowing) {\n        @content;\n    }\n}\n\n@mixin desktop-small-width {\n    @media (max-width: $desktop-small-width) {\n        @content;\n    }\n}\n\n@mixin media-tablet {\n    @media (max-width: $tablet-width) {\n        @content;\n    }\n}\n\n@mixin mobile-big-width {\n    @media (max-width: $mobile-big-width) {\n        @content;\n    }\n}\n@mixin mobile-small-width {\n    @media (max-width: $mobile-small-width) {\n        @content;\n    }\n}\n","@mixin heading-1($color: $color-text, $style: normal) {\n    font: {\n        family: 'Open Sans', sans-serif;\n        size: 6.4rem;\n        style: $style;\n        weight: 800;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin basic-text($color: $color-text, $style: normal) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 6.4rem;\n        style: $style;\n        weight: 600;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin input-placeholder($color: $color-border, $style: normal) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 1.2rem;\n        style: $style;\n        weight: 600;\n    }\n\n    line-height: 105%;\n    color: $color;\n    padding: 0.5rem;\n}\n\n@mixin input-text($color: $color-link, $style: normal) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 2rem;\n        style: $style;\n        weight: 600;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin button-text($color: $color-primary, $style: normal) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 2.4rem;\n        style: $style;\n        weight: 800;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin input() {\n    width: 21.4rem;\n    height: 3.3rem;\n    border: $color-border 2px solid;\n    border-radius: 0.6rem;\n    padding-left: 0.5rem;\n    padding-bottom: 0.5rem;\n    @include input-text;\n    &::placeholder {\n        @include input-placeholder($style: italic);\n        // color: $color-border-input;\n        // font-style: italic;\n        // padding-left: 1rem;\n    }\n}\n\n@mixin button-basic($color-bg: $color-light) {\n    display: block;\n    width: 15rem;\n    height: 8rem;\n    background-color: $color-bg;\n    border-radius: 1rem;\n    border: solid 2px $color-border;\n    text-align: center;\n    @include button-text;\n    cursor: pointer;\n\n    &:hover {\n        background-color: $color-additional;\n        color: $color-light;\n    }\n\n    &.active {\n        background-color: $color-primary;\n        color: $color-additional;\n    }\n}\n",".button-basic {\n    @include button-basic;\n}\n",".registration_container {\n    background-color: $color-light;\n    flex-basis: 50%;\n    // width: 700px;\n    // height: 700px;\n    margin: 15rem;\n    border-radius: 10px;\n}\n\n.registration {\n    display: flex;\n    flex-direction: column;\n    padding: 5rem;\n    gap: 3rem;\n    justify-content: center;\n    align-items: center;\n}\n\n.label_basic {\n    display: flex;\n    gap: 1rem;\n    justify-content: center;\n    align-items: center;\n    /*\n  font: {\n    family: 'Quicksand', sans-serif;\n    size: 0.9rem;\n    style: normal;\n    weight: 600;\n}\n\nline-height: 105%;\ncolor: $color-link;\n*/\n}\n\n.input_basic {\n    @include input;\n\n    &:focus {\n        background-color: #ff900893;\n        color: $color-primary;\n        border-color: $color-primary;\n        outline: 0;\n    }\n}\n\n.svg_user {\n    background-image: url('img/icon/user.svg');\n    display: block;\n    width: 2rem;\n    height: 2rem;\n}\n\n.svg_users {\n    background-image: url('img/icon/users.svg');\n    display: block;\n    width: 2rem;\n    height: 2rem;\n}\n\n.svg_email {\n    background-image: url('img/icon/mail.svg');\n    display: block;\n    width: 2rem;\n    height: 2rem;\n}\n\n.svg_password {\n    background-image: url('img/icon/lock.svg');\n    display: block;\n    width: 2rem;\n   